@import url(../fonts/BrandonGrotesque/stylesheet.css);
@import url(../fonts/GothamBook/stylesheet.css);

$ff-gotham: 'Gotham Book', 'Open Sans', Arial, sans-serif;
$ff-brandon: 'Brandon Grotesque', 'Open Sans', Arial, sans-serif;
$ff-opensans: 'Open Sans', Arial, sans-serif;

$orange: #ee3225;

$maroon: #9D2381;
$maroon-dark: #6F286B;
$purple: #4C2C66;

$green: #43B24B;
$green-dark: #13670B;

$red: #FF2B00;

$white: #fff;
$gray-light: #D3D3D3;
$gray-light-alt: #BBBCBD;
$gray: #707070;
$gray-dark: #414042;
$body: #333;
$black: #000;


body {
	font-family: $ff-brandon;
	color: $body;
	background-color: $white;
	font-weight: 500;
}

h1,.h1,
h2,.h2,
h3,.h3,
h4,.h4,
h5,.h5,
h6,.h6 {
	font-family: $ff-brandon;
	font-weight: 600;
}


h1,.h1 {
	font-size: 31px;
	letter-spacing: 3px;
}
h2,.h2 {
	font-size: 31px;
	letter-spacing: 3px;
}
h3,.h3 {
	font-size: 25px;
	font-weight: 800;
	letter-spacing: 2px;
}
h4,.h4 {
	font-size: 25px;
	font-weight: 600;
	letter-spacing: 2px;
	line-height: 1.2;
}
h5,.h5 {}
h6,.h6 {}

strong,b {
	font-family: inherit;
	font-weight: 600;
}

.color-orange {
	color: $orange;
}

.main-header {
	background-color: $gray-dark;
	color: $white;
	font-size: 14px;
	padding-top: 15px;
	padding-bottom: 15px;
	font-family: $ff-gotham;
	position: relative;

	.hamburger {
		position: absolute;
		top: 19px;
		left: 20px;
		width: 32px;
		height: 22px;
		cursor: pointer;

		span {
			display: block;
			width: 32px;
			height: 3px;
			margin-bottom: 6px;
			background-color: $white;
			border-radius: 1.5px;
		}
	}


	.header-logo {
		text-align: center;

		@media screen and (min-width: 64em) {
			text-align: left;
		}

		img {
			width: 407px;
			height: auto;
			display: inline-block;
			margin: 10px 0;

			@media screen and (max-width: 1023px) {
				width: 200px;
				margin: 0;
			}
		}
	}

	.header-tagline {
		text-align: center;
		margin: 0;

		@media screen and (min-width: 64em) {
			text-align: left;
		}

		@at-root {
			$a: #{&};
			body {
				&.page--terms, &.page--faqs {

					#{$a} {
						display: none;
					}

				}
			}
		}
	}

	.header-links {
		a {
			color: inherit;
			display: inline-block;
			margin-left: 5px;
			margin-right: 5px;
			margin-top: 7px;

			&:hover {
				text-decoration: underline;
			}
		}
	}
}

.main-content {
	padding-top: 50px;
	padding-bottom: 15px;

	@media screen and (max-width: 1023px) {
		padding-top: 20px;
	}

	@at-root {
		$a: #{&};
		body {
			&.page--front {
				background-image: url('../images/Swiss/splashbg_mob.jpg');
				background-color: $black;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: bottom center;
				color: $white;
				min-height: 100vh;

				@media screen and (min-width: 1024px) {
					background-image: url('../images/Swiss/splashbg.jpg');
					background-position: top center;
				}

				#{$a} {
					@media screen and (min-width: 64em) {
						padding-top: 20px;
					}
				}
			}

			&.page--start, &.page--contact-us, &[class*="page--messages--"] {

				background-image: url('../images/Swiss/mainbg.jpg');
				background-color: $black;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: top center;
				color: $white;
				min-height: 100vh;

				#{$a} {
					color: $white;
				}
			}

			&.page--closed {
				background-image: url('../images/Swiss/holdingbg.jpg');
				background-color: $black;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: top center;
				color: $white;
				min-height: 100vh;

				.h1 {
					font-weight: 100;
					strong {
						font-weight: 900;
					}
				}
			}

			&.page--map {
				background-image: url('../images/Swiss/mapbg.jpg');
				background-color: $black;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: top center;
				color: $white;
				min-height: 100vh;

				#{$a} {
					padding-top: 0;
					padding-bottom: 0;
				}

				.h1 {
					font-weight: 100;
					strong {
						font-weight: 900;
					}
				}

				.info-wrapper {
					position: absolute;
					left: 0;
					width: 100%;
					z-index: 2;

					@media screen and (max-width: 1023px) {
						position: relative;
						left: auto;
					}

					.info-window {
						margin-top: 40px;
						width: 340px;
						border: 3px solid $white;
						padding: 20px;
						font-size: 25px;
					    font-weight: 600;
					    letter-spacing: 2px;
					    line-height: 1.2;

					    @media screen and (max-width: 1023px) {
					    	margin-top: 20px;
					    	max-width: 100%;
					    	margin-left: auto;
					    	margin-right: auto;
					    	font-size: 20px;
					    }
					}

				}

				.map-wrapper {
					max-width: 1024px;
					position: relative;
					padding-top: 90px;
					padding-bottom: 50px;

					@media screen and (max-width: 1023px) {
						min-height: 0 !important;
						padding-top: 50px;
						background-image: url('../images/Swiss/mob_compass.png');
						background-position: center center;
						background-size: 75% auto;
						background-repeat: no-repeat;
					}

					> .columns {
						position: relative;

						@media screen and (max-width: 1023px) {
							padding-left: 0.83333rem;
							padding-right: 0.83333rem;
						}
					}

					img.map {
						width: 1024px;

						@media screen and (max-width: 1023px) {
							display: none;
						}
					}

					.map-link {
						text-align: center;

						@media screen and (max-width: 1023px) {
							a {
								max-width: 100px;
								display: inline-block;
								font-size: 20px;
								font-weight: 600;
								margin-bottom: 20px;
								position: relative;

								&:after {
									content: '';
									display: block;
									position: absolute;
									bottom: -5px;
									left: 50%;
									margin-left: -3px;
									width: 6px;
									height: 6px;
									border-radius: 3px;
									background-color: $orange;
								}
							}
						}

						@media screen and (min-width: 1024px) {
							position: absolute;
							transform: translate(-50%, -50%);

							a {
								display: block;
								min-width: 50px;
								height: 50px;
								// border: 2px solid #fff;
								cursor: pointer;

								span {
									display: none;
								}
							}

							&.d-usa {
								top: 195px;
								left: 312px;
							}
							&.d-spain {
							    top: 207px;
							    left: 498px;
							}
							&.d-italy {
								top: 211px;
							    left: 550px;
							}
							&.d-peru {
								top: 392px;
							    left: 314px;
							}
							&.d-nz {
								top: 475px;
							    left: 982px;
							}
						}
					}
				}
			}

			&[class*="page--map--"] {
				background-color: $black;
				color: $white;
				min-height: 100vh;

				#{$a} {
					padding-top: 0;
					padding-bottom: 0;
					position: relative;

					.bg-grid {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						z-index: -1;

						&:after {
							content: '';
							display: block;
							background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(0,0,0,0.65) 100%);
							background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.65) 100%);
							background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.65) 100%);
							filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6000000',GradientType=0 );
							position: absolute;
							bottom: 0;
							left: 0;
							width: 100%;
							height: 50%;
							max-height: 500px;
							z-index: 1;
						}

						.grid-item {
							position: absolute;
							width: 50%;
							height: 50%;
							background-position: center center;
							background-size: cover;
							background-repeat: no-repeat;
							z-index: 0;
						}

						.grid-item-1 {
							top: 0;
							left: 0;
						}
						.grid-item-2 {
							top: 0;
							right: 0;
						}
						.grid-item-3 {
							bottom: 0;
							left: 0;
						}
						.grid-item-4 {
							bottom: 0;
							right: 0;
						}
					}

					.info-wrapper {
						@media screen and (max-width: 767px) {
							padding-top: 1rem !important;
						}

						> .columns {
							max-width: 720px;
						}

						.info-window {
							background-color: $gray-dark;
							padding: 45px 60px;
							position: relative;
							margin-left: 20px;
							margin-right: 20px;

							@media screen and (max-width: 767px) {
								padding: 20px 25px 25px;
								margin-left: 30px;
								margin-right: 30px;
							}

							&:before,&:after {
								content: '';
								display: block;
								position: absolute;
								left: 0;
								width: 100%;
								height: 44px;
								z-index: 0;
								background-repeat: no-repeat;
								background-size: contain;
							}
							&:before {
								top: 0;
								background-image: url('../images/Swiss/info_corner_1.png'), url('../images/Swiss/info_corner_2.png');
								background-position: top left, top right
							}
							&:after {
								bottom: 0;
								background-image: url('../images/Swiss/info_corner_3.png'), url('../images/Swiss/info_corner_4.png');
								background-position: bottom left, bottom right
							}

							.info-title {
								font-size: 60px;
								margin-bottom: 0;

								@media screen and (max-width: 767px) {
									font-size: 30px;
								}
							}

							.info-sub {
								font-size: 30px;
								font-weight: 600;
								margin-bottom: 30px;

								@media screen and (max-width: 767px) {
									font-size: 16px;
									margin-bottom: 10px;
								}
							}

							.info-content {
								font-family: $ff-opensans;
								font-size: 16px;

								@media screen and (max-width: 767px) {
									font-size: 14px;
								}
							}

							ul {
								margin-left: 0;
								list-style-type: none;

								li {
									position: relative;
									margin-bottom: 20px;

									@media screen and (max-width: 767px) {
										margin-bottom: 10px;
									}

									&:before {
										content: '•';
										position: absolute;
										top: 0;
										left: -20px;
										color: $orange;
										font-size: 25px;
										line-height: 20px;
									}
								}
							}
						}
					}

					.info-footer {
						font-size: 15px;
						font-weight: 600;

						p {
							vertical-align: -3px;

							@media screen and (max-width: 1023px) {
								text-align: center;

								.button {
									margin-top: 10px;
								}
							}
						}
					}
				}
			}

			&.page--privacy, &.page--terms, &.page--faqs {

				#{$a} {
					font-family: $ff-opensans;

					h1,.h1 {
						font-size: 40px;
					}

					p {

					}

					a {
						text-decoration: underline;
					}

					h4 {
						color: $maroon-dark;
						font-size: 24px;
					}
				}

			}
		}
	}

	@media screen and (min-width: 64em) {
		padding-top: 70px;
		padding-bottom: 100px;
	}

	.page-title {
		color: $maroon-dark;
	}

	h1.header--cash-back {
		font-size: 84px;
		text-transform: uppercase;
		text-align: center;
		text-shadow: 0 5px 6px rgba(0,0,0,0.8);
		margin-bottom: 20px;

		@media screen and (min-width: 64em) {
			margin-bottom: 50px;
			margin-top: 100px;
		}
	}

	img.header--pack {
		width: 100%;
		height: auto;
		max-width: 520px;

		@media screen and (min-width: 64em) {
			max-width: 631px;
		}
	}

	a {
		color: inherit;
	}

	label {
		color: inherit;
	}

	.splash {

		@media screen and (min-width: 1024px) {
			margin-top: 50px;
		}

		.lockup {
			text-align: center;

			@media screen and (min-width: 1024px) {
				max-width: 600px;
			}

			img {
				width: 600px;

				@media screen and (max-width: 767px) {
					width: 360px;
				}
			}
		}

		.tagline {
			font-size: 30px;
			font-weight: 500;
			font-family: $ff-brandon;
			margin-top: 70px;

			@media screen and (max-width: 1023px) {
				text-align: center;
				font-size: 23px;
				margin-top: 15px;
				margin-bottom: 50px;
			}

			.button {
				margin-right: 10px;

				@media screen and (max-width: 1023px) {
					font-size: 23px;
					margin-bottom: 10px;
				}
			}
		}
	}

	.check-wrapper {
		text-align: center;
		margin-top: 20px;
		margin-bottom: 30px;

		@media screen and (min-width: 64em) {
			margin-top: 50px;
		}

		.check {
			display: inline-block;
			text-transform: uppercase;
			font-size: 12px;
			margin: 10px;

			@media screen and (min-width: 64em) {
				font-size: 17px;
			}

			img {
				width: 16px;
				height: auto;
				vertical-align: baseline;

				@media screen and (min-width: 64em) {
					width: 25px;
					vertical-align: text-top;
				}
			}
		}
	}

	.form-wrapper {
		// background-color: $purple;
		// box-shadow: 0 6px 10px rgba(0,0,0,0.16);
		padding: 40px 0;
		font-size: 15px;

		@media screen and (min-width: 1024px) {
			padding: 40px;
			font-size: 20px;
		}

		h2 {
			@media screen and (max-width: 1023px) {
				font-size: 18px;
			}
		}

		h3 {
			color: $orange;

			@media screen and (max-width: 1023px) {
				font-size: 18px;
			}
		}

		label {
			font-family: $ff-opensans;
			font-size: inherit;
		    line-height: inherit;
		    text-overflow: ellipsis;
		    white-space: nowrap;
		    // overflow: hidden;

		    &.error {
		    	color: $red;
		    	font-size: 16px;
		    	line-height: 1.1;
		    }

		    &[for="receipt"].button {
		    	background-color: $white;
		    	color: $gray;
		    	text-transform: none;
		    	padding: 0.44444rem;
		    	font-size: 0.88889rem;
		    	line-height: 2;
		    	font-weight: normal;
		    	text-align: left;
		    	border-radius: 0;

		    	@media screen and (max-width: 1023px) {
					padding: 10px;
					height: 40px;
					line-height: inherit;
				}

		    	span {
		    		color: $black;
		    	}
		    }
		}

		[type='text'], [type='password'], [type='date'], [type='datetime'], [type='datetime-local'], [type='month'], [type='week'], [type='email'], [type='number'], [type='search'], [type='tel'], [type='time'], [type='url'], [type='color'], textarea, select {
			border: 0 none;
			border-radius: 0;
			color: $black;
			font-size: inherit;
			font-family: $ff-opensans;
			padding-left: 15px;

			@media screen and (max-width: 1023px) {
				padding-left: 10px;
				height: 40px;
			}

			&::-webkit-input-placeholder {
			  color: $gray;
			}
			&::-moz-placeholder {
			  color: $gray;
			}
			&:-ms-input-placeholder {
			  color: $gray;
			}
			&:-moz-placeholder {
			  color: $gray;
			}

			&[disabled] {
				opacity: 0.8;
			}
		}

		.radio-group {
		    margin-right: -0.83333rem;
		    margin-left: -0.83333rem;

			&:after {
				content: '';
				display: table;
				clear: both;
			}

			.radio {
				float: left;
				width: 20%;

				label {
					cursor: pointer;
					overflow: visible;

					&:hover {
						.destination-icon {
							transform: scale(1.05);
						}
					}
				}

				input {
					display: none;

					&:checked {
						~ .radio-label {
							color: $orange;
						}
					}
				}

				.radio-label {
					display: block;
					text-align: center;
					text-transform: uppercase;
					font-weight: 600;
					font-size: 14px;
					letter-spacing: 1px;
					white-space: initial;

					@media screen and (min-width: 1024px) {
						font-size: 25px;
						letter-spacing: 2px;
					}
				}

				.required_hint {
					display: none;
				}

				.destination-icon {
					display: block;
					padding-bottom: 90%;
					background-repeat: no-repeat;
					background-position: center center;
					background-size: contain;
					margin-top: 20px;
					margin-bottom: 10px;
				}

				@each $i in italy, spain, peru, usa, nz, bay-of-islands {
					.destination-#{$i} {
						.destination-icon {
							background-image: url('../images/Swiss/#{$i}.png');
						}
					}
				}

				.destination-link {
					text-transform: uppercase;
					text-decoration: none;
					display: block;
					text-align: center;
					font-weight: 600;
					font-size: 10px;
					cursor: pointer;

					&:hover {
						text-decoration: underline;
					}

					@media screen and (min-width: 1024px) {
						font-size: 13px;
					}
				}

				.destination-bay-of-islands {
					margin-top: -21px;
				}
			}
		}

		.checkbox {
			font-size: 14px;

			@media screen and (min-width: 1024px) {
				font-size: 16px;
				line-height: 17px;
			}

			label {
				text-overflow: initial;
			    white-space: initial;
			    overflow: initial;

			    a {
			    	text-decoration: underline;
			    }

			    small {
			    	display: inline-block;
			    }
			}

			.check {
				border: 0 none;
				background-color: $white;
				border-radius: 0;
				color: $black;
				cursor: pointer;
				width: 25px;
				height: 25px;

				&:before {
					font: normal normal normal 14px/1 FontAwesome;
				    font-size: 20px;
				    text-rendering: auto;
				    -webkit-font-smoothing: antialiased;
				    -moz-osx-font-smoothing: grayscale;
					content: "\f00c";
					display: block;
					width: 25px;
					height: 25px;
					line-height: 25px;
					text-align: center;
					position: absolute;
					color: $white;
				}

				&:hover {
					&:before {
						color: $gray-light-alt;
					}
				}
			}

			input[type="checkbox"] {
				&:checked {
					~ .check {
						background-color: $white;

						&:before {
							color: $black;
						}
					}
				}
			}
		}

		.button.primary {
			background-color: transparent;
			color: $white;
			border: 3px solid $orange;
			letter-spacing: 3px;
			text-transform: uppercase;
			font-family: $ff-brandon;
			font-weight: 600;
			font-size: 30px;
			padding: 7px 30px;
		}

		.thank-you {
			font-size: 14px;
			line-height: 1.2;
		}
	}

	.button.primary {
		background-color: transparent;
		color: $white;
		border: 3px solid $orange;
		letter-spacing: 3px;
		text-transform: uppercase;
		font-family: $ff-brandon;
		font-weight: 600;
		font-size: 30px;
		padding: 7px 30px;
		vertical-align: inherit;

		&:hover {
			background-color: $orange;
		}
	}
}

.main-footer {
	background-color: $maroon;
	color: $white;
	font-size: 16px;
	padding-top: 30px;
	padding-bottom: 30px;

	a {
		color: $white;
	}

	.footer-logo {
