.mobile-menu {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.3);
	z-index: 99;
	display: none;

	.hamburger {
		position: absolute;
		top: 19px;
		left: 20px;
		width: 32px;
		height: 22px;
		cursor: pointer;
		z-index: 99;

		span {
			display: block;
			width: 32px;
			height: 3px;
			margin-bottom: -3px;
			background-color: $white;
			border-radius: 1.5px;
			transform: rotate(45deg);

			&:first-child {
				transform: rotate(45deg);
				margin-top: 10px;
			}
			&:last-child {
				transform: rotate(-45deg);
			}
		}
	}

	.mobile-links {
		position: absolute;
		width: 90%;
		height: 100%;
		background-color: $gray-dark;
		color: $white;
		padding: 90px 20px 40px;
		font-family: $ff-opensans;
		overflow: auto;

		.dest-links {
			margin-bottom: 20px;

			a {
				display: block;
				color: $white;
				margin-bottom: 20px;
				font-size: 24px;
				font-weight: 700;
				line-height: 1;
			}
		}

		.nav-links {
			a {
				display: block;
				color: $white;
				margin-bottom: 20px;
				font-size: 15px;
				line-height: 1;
			}
		}
	}
}

.datepicker-dropdown {
	box-shadow: 0 5px 6px rgba(0,0,0,0.2);
}

ul.af_list {
	color: $body;
	font-family: $ff-opensans;

	li.af_footer {
	    display: none !important;
	    width: 0px !important;
	    overflow: hidden !important;
	    padding: 0 !important;
	    max-height: 0px !important;
	}
}

.fancybox-bg {
	background-color: $black;
}

.fancybox-content {
	background-color: $gray-dark;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	padding: 0;
    margin-left: 20px;
    margin-right: 20px;
    position: relative;
    width: 600px;
    height: 600px;
    max-height: 95vh;
    max-width: calc(95vw - 52px);

    @media screen and (max-width: 767px) {
    	margin-left: 30px;
		margin-right: 30px;
		max-width: calc(95vw - 72px);
    }

    @media screen and (orientation: portrait) and (max-width: 767px) {
    	height: auto;
    	min-height: calc(95vw - 72px);
    	max-height: 95vh;
    }

    @each $i in italy, spain, peru, usa, nz, bay-of-islands {
		&#fb-#{$i} {
			background-image: url('../images/Swiss/fb-#{$i}.jpg');
		}
	}

    &:before,&:after {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		width: 100%;
		height: 44px;
		z-index: 0;
		background-repeat: no-repeat;
		background-size: contain;
	}
	&:before {
		top: 0;
		background-image: url('../images/Swiss/info_corner_1.png'), url('../images/Swiss/info_corner_2.png');
		background-position: top left, top right
	}
	&:after {
		bottom: 0;
		background-image: url('../images/Swiss/info_corner_3.png'), url('../images/Swiss/info_corner_4.png');
		background-position: bottom left, bottom right
	}

	.wrapper {
	    height: 600px;
	    max-height: 95vh;
	    position: relative;

	    @media screen and (orientation: portrait) and (max-width: 767px) {
	    	height: auto;
	    	min-height: calc(95vw - 72px);
	    }

		.inner {
			padding: 45px 40px;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			max-height: 100%;
			overflow: auto;

			@media screen and (max-width: 767px) {
				padding: 20px 25px 25px;
			}

			@media screen and (orientation: portrait) and (max-width: 767px) {
				position: relative;
				top: 0;
				transform: none;
			}

			h2 {
				font-size: 70px;
			    line-height: 1;

			    @media screen and (max-width: 767px) {
			    	font-size: 30px;
			    }
			}

			p {
				font-size: 20px;
				line-height: 1.2;
				font-weight: 500;

				@media screen and (max-width: 767px) {
					font-size: 16px;
				}
			}
		}
	}
}

.fancybox-can-swipe .fancybox-content,
.fancybox-can-pan .fancybox-content {
	cursor: initial;
}

.fancybox-slide::before {
	@media screen and (max-width: 671px) {
		// height: 0;
	}
}

